import Vue from 'vue'
import VueRouter from 'vue-router'
import Frontpage from "../views/Frontpage";

Vue.use(VueRouter)

const routes = [
  {path: '/', name: 'frontpage', component: Frontpage},
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
